        font-family: Roboto, sans-serif

        .title
            text-align: center
            font-size: 1.5rem
            margin-bottom: 2rem

        table
            width: 100%
            border-collapse: collapse
            border-top: 2px solid #d0d0d0

            tr
                &:nth-child(even)
                    background-color: #f2f2f2

            th, td
                transition: 0.5s
                text-align: left
                padding: 16px 8px

            .quantity-column
                width: 10px

                input
                    width: 40px
                    background-color: #ffffff00
                    border: 0
                    outline: none

            .quantity
                width: 60px

            .price
                width: 60px

        .comment
            display: block
            margin-top: 1.5rem
            margin-bottom: 0

            /*input
                width: 100%
                color: black
                padding: 10px
                font-size: 1rem
                box-shadow: unset
                border-radius: 3px
                border: 1px solid black

                &:focus
                    outline: none
                    border: 1px solid $secondary-color
                    box-shadow: 0 0 3px $secondary-color*/

        .bottom
            display: flex
            flex-direction: row
            align-content: flex-end
            margin-top: 1rem

            .total-price
                margin: auto 0 auto auto
                font-size: 1.1rem

                span
                    font-weight: bold

            button
                background-color: $secondary-color
                color: #FFFFFF
                min-width: 120px
